#page-wrap {
  text-align: center;
  overflow: auto;
}

.bm-item {
  display: inline-block;

  text-decoration: none;
  margin-bottom: 15px;
  color: ghostwhite;
  font-family: 'Amatic SC', cursive;
  transition: color 0.3s;
}

.bm-item:hover {
  color: white;
  text-decoration: underline;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 24px;
}

.bm-burger-bars {
  background: #4bcffa;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: rgba(0, 0, 0, 0.8);
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
