@import url(https://fonts.googleapis.com/css?family=Dosis);
@import url(https://fonts.googleapis.com/css?family=Maven+Pro);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(/sk/static/media/background2.b8d900f0.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: #2d3436;

  background-size: cover;
  color: ghostwhite;
  font-size-adjust: 0.5;
}

@media only screen and (max-width: 600px) {
  body {
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)), url(/sk/static/media/background2.b8d900f0.jpg);
    background-size: 400vw auto;
    background-position-x: right;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body-container {
  width: 100%;
  font-family: 'Maven Pro', sans-serif;
}

.body-container p {
  font-size: 130%;
  font-weight: 200;
}

.body {
  width: 86vw;
  min-height: 100vh;
  margin: 0 auto;
  display: flow-root;
  /* margin-top: 10px; */
  padding-left: 7vw;
  padding-right: 7vw;
  padding-top: 25px;
}

.about {
  background-color: #1F2833;
  color: white;
}

.projects {
  /* background-color: #116466; */
  background-color: #0B0C10;
  color: white;
}

.contact {
  /* background-color: #1F2833; */
  color: white;
  min-height: 30vw;
  display: flex;
  flex-direction: column;

  align-items: center;
}

.body > p {
  font-size: 120%;
}

.highlight-primary {
  color: #4bcffa;
  font-weight: bolder;
  font-family: 'Amatic SC', cursive;
}

.highlight-secondary {
  color: #4bcffa;
  font-size: 120%;
  font-weight: 900;
  font-family: 'Amatic SC', cursive;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.body-header {
  font-size: 3.0em;
  color: white;
  text-decoration: overline;
  font-weight: 400;
}

.divider {
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
  /* margin-top: 5%; */
  height: 1px;
  border-radius: 100px;
  background-color: #4bcffa;
}

#skills-container {
  width: 50%;
  float: left;
  padding-right: 1.5%;
}

.skillbar-percent {
  color: black;
}


@media only screen and (max-width: 600px) {
  #skills-container {
    width: 100%;
    float: left;
  }
}

.modal-overlay {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.7);
}

.modal-content {
  background-color: rgba(0,0,0);
  overflow: inherit;
  padding: 20px;
  border: 2px solid #888;
  border-radius: 10px;
  width: 60%;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.project-github-button {
  background-color: black;
  height: 60px;
  width: 300px;
  float: left;
  font-size: 36px;
  border-radius: 12px;
  border-color: #008CBA;
  margin: 10px;
  color: white;
  font-family: 'Amatic SC', cursive;
  transition: opacity 300ms, visibility 300ms;
  align-self: center;
}

@media only screen and (max-width: 900px) {
  .project-github-button {
    height: 60px;
    font-size: 24px;
    width: 150px;
  }

  .modal-content {
    width: 80%;
  }

  .modal-buttons {
    display: flex;
    margin-bottom: 20px;
  }
}

/* project modal buttons  */
.project-view-button {
  background-color: #008CBA;
  font-size: 36px;
  border-radius: 12px;
  color: black;
  position: absolute;
  visibility: hidden;
  opacity: 0;
  font-family: 'Amatic SC', cursive;
  transition: opacity 300ms, visibility 300ms;
}

/* project button on hover */
.project-cards:hover .project-view-button {
  visibility: visible;
  opacity: 1;
  color: white;
}

.project-name {
  display: inline;
  color: white;
  background-color: black;
}

.project-cards:hover .project-name {
  visibility: hidden;
  opacity: 1;
  color: white;
}

.project-cards:hover {
  background-color:rgba(0, 0, 0, 0.3)
}

.project-cards {
  width: 300px;
  height: 400px;
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;
  border-color: white;
  margin: 2%;
  padding: 5px;
  align-items: center;
  justify-content: center;
  display: flex;
  background-size: cover;
}

.welcome {
  height: 100vh;
  width: 100vw;
  text-align: center;
  vertical-align: middle;
  display:table-cell;
  vertical-align:middle;
  font-family: 'Amatic SC', cursive;
}

.social-logo {
  width: 56px;
  margin: 10px;
}

#page-wrap {
  text-align: center;
  overflow: auto;
}

.bm-item {
  display: inline-block;

  text-decoration: none;
  margin-bottom: 15px;
  color: ghostwhite;
  font-family: 'Amatic SC', cursive;
  transition: color 0.3s;
}

.bm-item:hover {
  color: white;
  text-decoration: underline;
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 24px;
}

.bm-burger-bars {
  background: #4bcffa;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background: #bdc3c7;
}

.bm-menu {
  background: rgba(0, 0, 0, 0.8);
  padding: 2.5em 1.5em 0;
  font-size: 1.5em;
}

.bm-morph-shape {
  fill: #373a47;
}

.bm-item-list {
  color: #b8b7ad;
}

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

