@import url('https://fonts.googleapis.com/css?family=Maven+Pro');


.body-container {
  width: 100%;
  font-family: 'Maven Pro', sans-serif;
}

.body-container p {
  font-size: 130%;
  font-weight: 200;
}

.body {
  width: 86vw;
  min-height: 100vh;
  margin: 0 auto;
  display: flow-root;
  /* margin-top: 10px; */
  padding-left: 7vw;
  padding-right: 7vw;
  padding-top: 25px;
}

.about {
  background-color: #1F2833;
  color: white;
}

.projects {
  /* background-color: #116466; */
  background-color: #0B0C10;
  color: white;
}

.contact {
  /* background-color: #1F2833; */
  color: white;
  min-height: 30vw;
  display: flex;
  flex-direction: column;

  align-items: center;
}

.body > p {
  font-size: 120%;
}

.highlight-primary {
  color: #4bcffa;
  font-weight: bolder;
  font-family: 'Amatic SC', cursive;
}

.highlight-secondary {
  color: #4bcffa;
  font-size: 120%;
  font-weight: 900;
  font-family: 'Amatic SC', cursive;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.body-header {
  font-size: 3.0em;
  color: white;
  text-decoration: overline;
  font-weight: 400;
}

.divider {
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
  /* margin-top: 5%; */
  height: 1px;
  border-radius: 100px;
  background-color: #4bcffa;
}
